/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.eltdf-woo-pagination-holder {
    @include eltdfRelativeHolderLayout();
    margin: 26px 0 14px;
    clear: both;

    .eltdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0 0 30px;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;
	    text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 9px;

            a,
            span {
                position: relative;
                display: inline-block;
                margin: 0;
                padding: 0;
                vertical-align: middle;
	            font-family: $default-heading-font;
	            font-size: 19px;
	            font-weight: 700;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: $default-heading-color;
                @include eltdfTransition(color .2s ease-out);
                min-width: 37px;
                height: 37px;
                line-height: 37px;
                text-align: center;
                
                &:after {
                    position: absolute;
                    content: '';
                    top: 0;
                    left: 0;
                    width: 37px;
                    height: 37px;
                    border-radius: 100%;
                    opacity: 0;
                    background-color: $first-main-color;
                    z-index: -1;
                    @include eltdfTransform(scale(0) translateZ(0));
                    @include eltdfTransitionTransform(.3s $default-easing, opacity .3s ease);
                }

                &:hover,
                &.current {
                    color: #fff;
                    
                    &:after {
                        opacity: 1;
                        @include eltdfTransform(scale(1) translateZ(0));
                    }
                }
            }

            a {
				line-height: 37px;
	            
                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0;

                    &:before {
                        position: relative;
                        top: 0;
                        display: block;
                        font-family: 'fontAwesome';
                        font-size: 19px;
                        line-height: 37px;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
                    
                    &:before {
                        content: "\f104";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f105";
                    }
                }
            }
        }
    }
}

.eltdf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */