/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 6px);
								padding: 0 0 24px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (6 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
	
	.product {
		@include eltdfRelativeHolderLayout();
		float: none;
		margin: 0 0 41px;
	}
	
	.eltdf-single-product-content {
		@include eltdfRelativeHolderLayout();
		clear: both;
		
		.images {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 51%;
			margin: 0;
			padding: 0 30px 0 0;
			float: left;
			box-sizing: border-box;
			
			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -12px;
				
				a {
					@include eltdfRelativeHolderLayout();
					
					img {
						position: relative;
						width: 100%;
						display: block;
					}
				}
			}
			
			.woocommerce-product-gallery__trigger {
				position: absolute;
				top: 10px;
				right: 39px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				z-index: 10;
			}
			
			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 12px;
				box-sizing: border-box;
				
				&:first-child {
					left: 12px;
					width: calc(100% - 24px);
					padding: 0;
				}
				
				.zoomImg {
					cursor: crosshair;
				}
			}
			
			&.woocommerce-product-gallery--with-images {
				
				.woocommerce-product-gallery__image {
					
					&:not(:first-child) {
						float: left;
						padding-top: 24px;
					}
				}
			}
			
			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+2);
					
					.woocommerce-product-gallery__image {
						
						&:not(:first-child) {
							width: $column_width;
						}
					}
				}
			}
		}
	}
	
	.eltdf-single-product-summary {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 49%;
		padding: 0 0 0 48px;
		float: left;
		box-sizing: border-box;
		
		.eltdf-single-product-title {
			display: block;
			margin: 0;
		}
		
		.price {
			@include eltdfWooCommercePriceStyle(50px, 20px, $default-heading-font, 500, $default-heading-color);
			margin: 0;
			
			del {
				top: 5px;
				
				.amount {
					line-height: 1;
				}
				
				.woocommerce-Price-currencySymbol {
					font-size: 13px;
				}
			}
			
			> .woocommerce-Price-amount,
			ins {
				
				.woocommerce-Price-currencySymbol {
					top: -5px;
					font-size: 16px;
				}
			}
			
			.woocommerce-Price-currencySymbol {
				vertical-align: super;
			}
		}
		
		.woocommerce-product-rating {
			@include eltdfRelativeHolderLayout();
			line-height: 1;
			margin: 9px 0 0;
		}
		
		.woocommerce-product-details__short-description {
			@include eltdfRelativeHolderLayout();
			margin: 38px 0 0;
			
			p {
				margin: 0;
			}
		}
		
		.product_meta {
			margin: 43px 0 0;
			
			> span {
				display: block;
				margin: 3px 0;
				
				.eltdf-woo-meta-title {
					margin-right: 10px;
					font-family: $default-heading-font;
					font-weight: 700;
					letter-spacing: .01em;
					color: $default-heading-color;
				}
				
				a {
					color: $default-text-color;
					
					&:hover {
						color: $first-main-color;
					}
				}
			}
		}
		
		.eltdf-woo-social-share-holder {
			@include eltdfRelativeHolderLayout();
			margin: 0;
			
			> * {
				display: table-cell;
				vertical-align: top;
			}
			
			> span {
				padding: 0 9px 0 0;
			}
			
			.eltdf-social-share-holder ul {
				margin-left: 5px;
			}
		}
		
		p.stock.out-of-stock,
		p.stock.in-stock {
			color: $default-heading-color;
			font-weight: bold;
			margin: 18px 0 0;
		}
		
		form.cart {
			@include eltdfRelativeHolderLayout();
			margin: 44px 0 0;
		}
		
		.eltdf-quantity-buttons {
			margin: 0 14px 0 0;
		}
	}
	
	.eltdf-accordion-holder {
		@include eltdfRelativeHolderLayout();
		margin: 40px 0 0;
		
		.eltdf-accordion-content {
			padding: 22px 0 25px;
			@include eltdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 0;
			}
		}
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-top: 1px solid $default-border-color;
			border-bottom: 1px solid $default-border-color;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px;
				}
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
				font-family: $default-heading-font;
				color: $default-heading-color;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 71px 0 0;
		
		> h2 {
			font-size: 30px;
			line-height: 1.004em;
			margin: 0 0 37px;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */