/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 7px;
    width: 55px;
    height: 55px;
    margin: 0;
    padding: 0;
    color: #fff;
	font-family: $default-heading-font;
    font-size: 15px;
	font-weight: 700;
    line-height: 52px;
	letter-spacing: .01em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
	border-radius: 100%;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    left: 7px;
    background-color: $second_main-color;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    left: 7px;
    background-color: $first-main-color;
}

@mixin eltdfWooCommerceNewProductStyle() {
    right: 7px;
    background-color: $default-heading-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle($big_size: 15px, $small_size: 15px, $font: inherit, $font_weight: 400, $color: $default-text-color) {
    position: relative;
    display: inline-block;
	vertical-align: top;
    margin: 11px 0 0;
	font-family: $font;
	font-size: $big_size;
    line-height: 1.2em;
	font-weight: $font_weight;
    color: $color;
	
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
	    position: relative;
	    float: right;
	    font-size: $small_size;
	    text-decoration: none;
	    color: rgba($color, .8);
	
	    .woocommerce-Price-amount:after {
		    content: '';
		    position: absolute;
		    top: 50%;
		    left: 0;
		    width: 100%;
		    height: 1px;
		    background-color: $second-main-color;
	    }
    }
    
    ins {
        text-decoration: none;
	    margin-right: 10px;
    }
	
	.woocommerce-Price-currencySymbol {
		position: relative;
		margin-right: 5px;
	}
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "fontAwesome";
    font-size: 12px;
    line-height: 1;
    letter-spacing: .4em;
    color: $second-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f006\f006\f006\f006\f006";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f005\f005\f005\f005\f005";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f006";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f005\f005\f005\f005\f005";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */