/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page {
	
	table.cart {
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid #D7D7D7;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					font-family: $default-heading-font;
					text-align: left;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid #D7D7D7;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-name {
					a {
						font-family: $default-heading-font;
					}
				}
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 35px 0 36px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				
				label {
					display: none;
				}
				
				input[type="text"] {
					margin: 0 22px 0 0;
					line-height: 30px;
				}
			}
			
			> .button {
				
				@media only screen and (min-width: $phone-landscape-plus-pixel) {
					float: right;
				}
			}
		}
	}
	
	.cart-collaterals {
		@include eltdfRelativeHolderLayout;
		margin: 0 0 87px;
		
		h2 {
			position: relative;
			display: block;
			margin: 0 0 26px;
		}
		
		table {
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid #D7D7D7;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 20%;
				padding-right: 5px;
				border: 0;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin-top: 15px;
			}
			p {
				margin: 0;
			}
			.shipping-calculator-form {
				#calc_shipping_country_field {
					margin: 20px 0 10px 0;
				}
			}
		}
	}
	
	.cross-sells,
	.cart_totals {
		margin: 71px 0 0;
		
		> h2 {
			margin: 0 0 31px;
		}
	}
	
	.cart_totals {
		.shop_table {
			th,
			.order-total {
				color: $default-heading-color;
				font-family: $default-heading-font;
				
				.woocommerce-Price-amount {
					font-weight: 500;
				}
				
			}
		}
	}
}

.eltdf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		font-size: 30px;
		font-weight: 500;
		line-height: 1.333333333333333em;
		text-align: center;
		color: $default-heading-color;
		margin: 40px 0 33px;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */