/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
         color: $second-main-color;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    .eltdf-cart-title {
        color: $default-heading-color;
        font-size: 16px;
        font-weight: 500;
        font-family: $default-heading-font;
        margin-right: 10px;
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            @include eltdfTransform(scale(1));
            @include eltdfTransitionTransform(.15s cubic-bezier(.1,1.26,.83,1),opacity .1s cubic-bezier(.39,.575,.565,1));
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
      color: $header-light-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart .eltdf-cart-title {
    color: $header-light-color;

    &:hover {
       color: $header-light-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: -4px;
    width: 308px;
    background-color: #fff;
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.03);
    border-radius: 7px;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: visible;
    box-sizing: border-box;
    @include eltdfTransform(scale(.4));
    @include eltdfTransformOrigin(100% top);

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 20px 0;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            &.eltdf-empty-cart {
	            font-size: 13px;
                font-weight: 400;
                border-bottom: 0;
	            color: #8f8f8f;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
            border-radius: 5px;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
	        line-height: 1.2em;
        }

        .remove {
            position: absolute;
            top: 5px;
            right: 0;
	        font-size: 18px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 4px 0 0;
            font-weight: 400;
            color: #8f8f8f;
        }

        .amount {
            display: block;
            margin: 4px 0 0;
            font-family: $default-heading-font;
            color: $default-heading-color;
            font-weight: 500;
        }
    }

    .eltdf-cart-bottom {
        padding: 19px 0 0;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 20px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #8f8f8f;
                font-weight: 400;
                line-height: 20px;
            }

            .eltdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            height: 44px;
            line-height: 44px;
            padding: 0 26px;
            border-radius: 5px;
            font-family: $default-heading-font;
            font-size: 14px;
            font-weight: 500;
	        letter-spacing: .05em;
            color: #fff;
            background-color: $second-main-color;
            text-align: center;
            text-transform: lowercase;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */