/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include eltdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
	            
	            a img {
		            border-radius: 5px;
	            }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
	
	    .eltdf-widget-title {
		    margin: 0 0 23px;
	    }
        
        .widget_shopping_cart_content {
            @include eltdfRelativeHolderLayout();
            
            ul {
                
                li {
                    a {
	                    font-weight: 500;
                    }
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include eltdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
	        
	        .woocommerce-mini-cart__buttons {
                .button.wc-forward {
		        padding: 3px 31px;
		        margin-right: 10px;
		        margin-bottom: 10px;
		        text-transform: capitalize;
                }
            }
            
            .total {
                margin: 17px 0 13px;
	            color: $default-heading-color;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .eltdf-widget-title {
            margin: 0 0 7px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .eltdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
	
	    .eltdf-widget-title {
		    margin-bottom: 13px;
	    }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -5px;
                    height: 13px;
                    width: 13px;
                    border-radius: 100%;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 20px 0 0;
	        line-height: 38px;
            
            .button {
                float: right;
	            padding: 3px 31px;
            }
            
            .price_label {
                float: left;
                font-family: $default-heading-font;
	            font-size: 16px;
	            font-weight: 500;
	            color: $default-heading-color;
            }
        }
    }
    
    &.widget_product_categories {
	
	    .eltdf-widget-title {
		    margin-bottom: 7px;
	    }
        
        ul {
            
            li {
                margin: 2px 0;
	            
	            a {
		            color: $default-text-color;
		            
		            &:hover {
			            color: $default-heading-color;
		            }
	            }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .eltdf-widget-title {
            margin-bottom: 23px;
        }
        
        ul {
            
            li {
                min-height: 85px;
                
                a {
	                font-family: $default-heading-font;
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 97px;
	                    font-family: $default-heading-font;
	                    font-weight: 500;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 97px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
	                text-decoration-color: $second-main-color;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 7px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 97px;
	        font-weight: 500;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
	            
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
	
	    .eltdf-widget-title {
		    margin-bottom: 7px;
	    }
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
	            font-size: 15px !important;
	            text-transform: capitalize;
	            background-color: transparent;
	            color: $default-text-color;
             
	            &:hover {
		            color: $first-main-color;
	            }
	            
                &:after {
                    content: ',';
                    color: $default-text-color;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include eltdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include eltdfRelativeHolderLayout();
                border: 2px dotted $first-main-color;
	            border-radius: 5px;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-heading-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include eltdfTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: $default-heading-font;
                font-size: 16px;
	            font-weight: 500;
    
                @include eltdfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 15px;
                cursor: pointer;
                
                &:hover {
                    color: $first-main-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
	&.widget_rating_filter {
		.eltdf-widget-title {
			margin-bottom: 11px;
		}
	}
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */